@import url("https://fonts.googleapis.com/css2?family=Satisfy&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&family=Satisfy&display=swap");

:root {
  --blue1: rgba(74, 226, 226, 0.795);
  --blue2: rgb(11, 135, 167);
  --blue3: rgb(7, 86, 105);
  --blue4: rgb(4, 62, 70);
  --white1: rgb(195, 248, 250);
  --white2: rgb(189, 245, 247);
  --white3: rgb(226, 247, 250);
  --white4: rgb(255, 255, 255);
  --grey1: rgba(154, 243, 247, 0.473);
  --grey2: rgba(193, 241, 243, 0.774);
  --grey3: rgb(193, 241, 243);
  --grey4: rgba(82, 108, 109, 0.973);
  --footer: rgb(2, 27, 29);
  --transition: all 0.5s linear;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: content-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* =========
    Global  
   ========= */

main {
  min-height: 100vh;
}
a {
  text-decoration: none;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
h1 {
  font-size: 3rem;
}
h1,
h2,
h3,
h4 {
  text-transform: capitalize;
}

button {
  text-transform: capitalize;
}

header {
  margin: 10px;
  padding: 5px;
}
.underline {
  width: 10rem;
  background-color: var(--blue3);
  height: 5px;
  margin: 0 auto;
}

.icon {
  font-size: 2.5rem;
  padding: 10px;
  border-radius: 50%;
  color: var(--blue4);
  background-color: var(--white1);
}

.img-container {
  position: relative;
}

@media screen and (max-width: 900px) {
}

/* =========
    Loading 
   ========= */

.loading {
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  margin-top: 10rem;
  border-radius: 50%;
  border: 3px solid var(--grey1);
  border-top-color: var(--blue1);
  animation: spinner 0.6s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(360deg);
  }
}

/* =========
    white1 
   ========= */

.nav-bar {
  margin: 0 auto;
  width: 100%;
  background-color: var(--white1);
  box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.5);
}
.nav-center {
  margin: 0 4rem;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.user-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-family: "Satisfy", cursive;
  font-size: 1.7rem;
  color: var(--blue2);
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
}
.logo span {
  color: var(--blue3);
}

.btn {
  background-color: transparent;
  border-style: none;
  height: 3rem;
  font-size: 1rem;
  margin: 0 0.5rem;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  color: var(--blue3);
  font-weight: bolder;
  transition: var(--transition);
}

.btn:hover {
  background-color: var(--blue3);
  color: var(--white1);
  cursor: pointer;
}

.toggle-btn {
  display: none;
}

.user-btn {
  display: grid;
  place-items: center;
  grid-template-columns: auto auto;
  column-gap: 5px;
  font-size: 1.5rem;
}

@media screen and (max-width: 900px) {
  nav .links-container {
    display: none;
  }
  nav .user-container {
    display: none;
  }
  .toggle-btn {
    display: flex;
    height: 100%;
    font-size: 2rem;
    align-items: center;
    justify-content: center;
    transition: var(--transition);
  }
  .toggle-btn:hover {
    transform: rotate(90deg);
    background-color: transparent;
    color: var(--blue3);
  }
  .nav-center {
    margin: 0 1.5rem;
  }
}

/* =========
    Sidebar 
   ========= */

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  transition: var(--transition);
  transform: translateX(-100%);
  overflow: hidden;
  z-index: 999;
}

.show-sidebar {
  transform: translateX(0);
}

.close-sidebar {
  position: fixed;
  top: 3vh;
  right: 3vw;
  color: red;
  font-size: 1.5rem;
  border-style: none;
  background-color: transparent;
}

.sidebar-center {
  background-color: var(--grey3);
  height: 90vh;
  width: 90vw;
  display: grid;
  grid-template-rows: 20vh 40vh;
  place-items: center;
  padding: 10px;
}

aside .links-container {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  width: 100%;
}

aside .user-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
}

/* =========
      Home 
   ========= */

.home-center {
  margin-top: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--white3);
  height: 100vh;
}

.start-page {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5px;
  max-width: 60vw;
}

.shop-now {
  background-color: var(--blue2);
  border-style: none;
  color: white;
  height: 2.5rem;
  width: 10rem;
  padding: 5px;
  font-size: 2rem;
  border-radius: 0.25rem;
  transition: var(--transition);
  text-transform: capitalize;
}

.shop-now:hover {
  background-color: var(--blue3);
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.2);
}

.page-info {
  display: grid;
  row-gap: 5px;
}

/* ============ */

.featured-products {
  display: grid;
  place-items: center;
  row-gap: 5px;
  background-color: var(--white4);
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
  column-gap: 5px;
  row-gap: 5px;
  place-items: center;
  max-width: 70vw;

  /* display: flex;
  min-width: 70vw;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 10px; */
}

.info-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.price {
  color: var(--blue3);
  font-weight: 650;
}

.img-overlay {
  display: flex;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  transition: var(--transition);
  opacity: 0;
}

.img-overlay:hover {
  opacity: 1;
}

.search-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.5rem;
  width: 2.5rem;
  color: white;
  outline: none;
  font-size: 1.2rem;
  border-style: none;
  border-radius: 50%;
  cursor: pointer;
  background-color: var(--blue2);
}

.all-products {
  background-color: var(--blue2);
  color: white;
  border-radius: 0.25rem;
  height: 1.75rem;
  padding: 0.5rem;
}

.all-products:hover {
  background-color: var(--blue3);
}

@media screen and (max-width: 900px) {
  .start-page {
    max-width: 90vw;
  }
  .grid-container {
    max-width: 100%;
  }
}

/* =========== */

.about-us {
  margin-top: 5px;
  padding: 15px;
  min-height: 50vh;
  background-color: var(--grey3);
  display: grid;
  place-items: center;
  grid-template-rows: 70px 70px auto;
}

.about-us .grid-container {
  flex-direction: row;
}

.vision,
.mission,
.history {
  display: flex;
  height: 300px;
  flex-direction: column;
  background-color: var(--blue2);
  align-items: center;
  justify-content: space-evenly;
  border-radius: 0.25rem;
  color: var(--blue4);
  padding: 10px;
  margin: 10px;
}
.vision,
.mission,
.history p {
  font-weight: 600;
}

/* ======
   Footer
   ====== */

footer {
  height: 70px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--footer);
}

/* ======
  Products
   ====== */
.products-header {
  display: flex;
  align-items: center;
  font-family: Roboto;
  height: 15vh;
  margin: 0;
  margin-top: 3px;
  background-color: var(--blue1);
}

.products-header span {
  color: var(--blue2);
  transition: var(--transition);
}

.products-header span:hover {
  color: var(--blue3);
}

.products-header h1 {
  color: var(--blue3);
}

.products-center {
  margin-top: 1.5rem;
  display: grid;
  grid-template-areas: "a b" "a c";
  min-height: 90vh;
}

.products-grid,
.products-list {
  grid-area: c;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 10px;
  column-gap: 10px;
}

.products-list {
  grid-template-columns: auto;
}

.product-grid {
  display: grid;
  max-width: 60vw;
  place-items: center;
  row-gap: 10px;
  column-gap: 10px;
}

@media screen and (min-width: 1000px) {
  .product-grid {
    grid-template-columns: 30% 70%;
  }
}

@media screen and (max-width: 1000px) {
  .products-grid {
    grid-template-columns: 50% 50%;
  }
}

@media screen and (max-width: 768px) {
  .products-grid {
    grid-template-columns: auto;
  }
}
/* ===========
   Filters bar
   =========== */

.config-bar {
  grid-area: a;
  padding: 2rem;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  display: grid;
  grid-template-rows: auto;
  row-gap: 1rem;
  height: 40vh;
  z-index: 3;
}

.categories {
  display: grid;
  row-gap: 5px;
  grid-template-rows: 1fr;
}

.category {
  height: 1.5rem;
  color: var(--blue3);
  border-style: none;
  font-weight: 650;
  font-family: Roboto;
  font-size: 1rem;
  text-align: left;
  background-color: transparent;
  cursor: pointer;
}

.search-input {
  height: 2rem;
  margin-bottom: 1rem;
  border-radius: 0.25rem;
  border-style: none;
  background-color: var(--grey2);
}

input::placeholder {
  font-size: 1rem;
  padding: 10px;
  font-family: Roboto;
  color: var(--blue3);
}

.companies,
.sort-options-menu {
  margin: 1rem 0;
  height: 1.5rem;
  border-radius: 0.25rem;
  width: 7rem;
}

.options {
  grid-area: b;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 6rem;
}

.options p {
  color: var(--grey4);
  font-weight: 550;
}

.options-btn {
  display: grid;
  grid-template-columns: auto auto auto;
  column-gap: 10px;
}

.sort-options {
  display: grid;
  place-items: center;
  grid-template-columns: auto auto;
  column-gap: 10px;
}

.show-list,
.show-grid {
  display: flex;
  align-items: center;
  color: var(--blue1);
  font-size: 1.5rem;
  border-style: none;
  text-align: center;
  background-color: transparent;
  transition: var(--transition);
}

.show-list:hover,
.show-grid:hover {
  color: var(--blue3);
  background-color: var(--grey1);
}

@media screen and (max-width: 900px) {
  .products {
    grid-template-columns: 1fr;
  }
}
